.incompleteSection {
    grid-row: 1;
    max-width: 90vw;
}

.backGroup {
    display: flex;
    width: 20vw;
    align-items: center;
    padding-right: 5px;
    border-radius: 12px;
}

.backGroup:hover {
    background: white;
}

.backLabel{
    padding-left: 5px;
    font-weight: bolder;
    font-size: small;
}


.sortSelect{
    font-family: -apple-system, Helvetica;
    padding-left: 2px;
    border: 1px solid rgba(1, 50, 67, 0.4);
    width: 90px;
    color: #14274E;
    border-radius: 12px;
    height: 24px;
    font-size: 12px;
    justify-content: center;
    align-content: center;
    background-color: rgba(1, 50, 67, 0.02);
}

.sortSelect select:focus {
    outline: none;
}



.confirmButton{
    background-color: #3D9970;
    border-color: #3D9970;
    color: #F1F6F9;
    position: relative;
    right: 5px;
    padding-left: 6px;
    padding-right: 6px;
    border-style: none;
    border-radius: 12px;
    margin-left: auto;
    height: 22px;
}

.cancel{
    background: rgba(1, 50, 67, 0.08);
    border-radius: 12px;
    height: 22px;
}
.cancel:hover{
    border-radius: 12px;
    height: 22px;
}